<template>
    <div v-theme:column="'wide'" id="show-blogs">
        <h1>博客总览</h1>
        <input type="text" v-model="search" placeholder="搜索">
        <div v-for="(blog, index) in filterBlogs" class="single-blog" :key="index">
            <router-link :to="'/blog/' + blog.id">
                <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
            </router-link>
            <article>
                {{blog.content | snippet}}
            </article>
        </div>
    </div>
</template>

<script>
export default {
    name: 'show-blogs',
    data() {
        return {
            blogs: [],
            search: ""
        }
    },
    created() {
        this.$http
                // .get("https://jsonplaceholder.typicode.com/posts")
                // .get("./../static/posts.json")
                .get("https://vuedemo-1f192-default-rtdb.firebaseio.com/posts.json")
                .then((data) => {
                    // console.log(data); // 是一个对象，不再是一个数组
                    // this.blogs = data.body.slice(0, 10);
                    return data.json(); // 返回一个promise
                })
                .then(data => {
                    var blogsArray = [];
                    for (let key in data) {
                        data[key].id = key;
                        blogsArray.push(data[key]);
                    }
                    this.blogs = blogsArray.slice(0, 10);
                })
    },
    computed: {
        filterBlogs: function() {
            return this.blogs.filter(blog=>{
                return blog.title.match(this.search);
            })
        }
    },
    filters: {
        // "to-uppercase": function(value) {
        //     return value.toUpperCase();
        // }
        toUppercase(value) {
            return value.toUpperCase();
        }
    },
    directives: {
        'rainbow': {
            bind(el, binding, vnode) {
                el.style.color = "#" + Math.random().toString(16).slice(2, 8);
            }
        }
    }
}
</script>

<style>
#show-blogs {
    max-width: 800px;
    margin:0 auto;
}

.single-blog {
    padding:20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border:1px dotted #aaa;
}

#show-blogs a {
    color:#444;
    text-decoration: none;
    cursor: pointer;
}

input[type='text'] {
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
}
</style>




